}
.btn-hover.btn-hover-animate:before {
  content: "\f417";
  font-family: bootstrap-icons !important;
  font-size: 0.8125rem;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  inset-inline-end: 0;
  inset-block-start: 0;
  opacity: 0;
  height: 100%;
  width: 2rem;
  transition: all 0.2s linear 0s;
}
